<template>
  <div class="Cucoloris">
    <i class="el-icon-back allback" @click="$emit('back')"></i>
    <!-- 瀑布流布局测试区域 -->
    <div class="pubuliu">
      <div
        :class="['item',item.isdel != 'false' ? 'isdeletei':'']"
        v-for="(item,index) in imgdata"
        :key="index"
      >
        <div :class="{'zhezao':true,'show':showdelete}">
          <el-popconfirm title="您确定要删除这张剪影吗？" class="delete" @onConfirm="deleteimg(index,item.id)">
            <el-button slot="reference" type="danger" icon="el-icon-delete" circle></el-button>
          </el-popconfirm>
        </div>
        <div class="item-content">
          <img
            @click="setAppimgFlage([true,item.img])"
            :src="item.img | imgFilter"
            alt
            class="item_img"
          />
          <div class="img_bottom">
            {{item.text}}
            <i
              :class="['el-icon-apple',item.truezan?'active':'']"
              @click="iactivefn(index)"
            >
              <span class="num">{{item.zan}}</span>
            </i>
          </div>
        </div>
      </div>
      <div class="item" v-if="isLogin">
        <div
          class="item-content delete_btn"
          :style="showdelete?'border-color:skyblue;background-color:#ffeebc':''"
        >
          <el-button
            @click="showdelete = !showdelete"
            class="delete"
            type="info"
            icon="el-icon-delete"
            circle
          ></el-button>
        </div>
      </div>
      <div class="item" v-if="isLogin">
        <div class="item-content addimg">
          <el-upload
            class="avatar-uploader"
            :action="$http.defaults.baseURL+'/uplod'"
            name="img"
            :show-file-list="false"
            :on-success="upsucceed"
            :on-progress="headupload"
            :on-error="uperror"
          >
            <img v-if="imageUrl" :src="imageUrl | imgFilter" class="avatar" />
            <i v-else class="el-icon-plus avatar-uploader-icon"></i>
          </el-upload>
          <br />
          <i>剪影标题：</i>
          <br />
          <el-input
            style="margin-top:4px;"
            maxlength="8"
            v-model="img_title"
            placeholder="请输入8字标题"
            clearable
          ></el-input>
          <br />
          <el-button @click="addimg" type="warning" size="mini" round style="float:right;margin-top:5px;">保 存</el-button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
// 引入vuex
import { mapState, mapMutations } from "vuex";
export default {
  computed: {
    ...mapState([
      "allnavs", //用户导航权限
      "isLogin", //是否登录状态
      "USER_ID" //用户ID
    ])
  },
  data() {
    return {
      imgdata: [],
      showdelete: false,
      img_title: "点个赞吧~",
      imageUrl: "",
      time:''
    };
  },
  methods: {
    ...mapMutations([
      "setAppimgFlage", //修改图片查看器的开关 参数 [true/false,imgurl]
      "setLoadingFlage" //加载开关
    ]),
    //   点赞
    async iactivefn(index) {
      // 判断
      if (this.imgdata[index].truezan) {
        this.$notify({
          title: "已经点赞过了哟~",
          type: "warning",
          customClass: "alter"
        });
        return;
      }
      // 缓存当前点赞数量
      let currentzan = this.imgdata[index].zan;
      this.imgdata[index].truezan = true;
      this.imgdata[index].zan += 1;
      //
      let { data: res } = await this.$http.post("/imgs/zan", {
        id: this.imgdata[index].id
      });
      // 判断
      if (res.ok == 0) {
        this.imgdata[index].truezan = false;
        this.imgdata[index].zan = currentzan;
        this.$notify.error({
          title: "修改失败",
          customClass: "alter"
        });
      } else {
        this.$notify.info({
          title: "感谢您的肯定",
          customClass: "alter"
        });
      }
    },
    // 加载
    async load() {
      console.log(this.USER_ID)
      this.setLoadingFlage(true);
      let { data: res } = await this.$http.get("/imgs/" + this.USER_ID);
        this.imgdata = res.data;
      this.setLoadingFlage(false);
    },
    // 删除图片
    async deleteimg(index,id) {
      this.imgdata[index].isdel = true;
      let {data:res} = await this.$http.delete('/imgs/'+id)
      if(res.ok == 0){
        setTimeout(() => {
          this.imgdata[index].isdel = 'false'
        }, 1000);
        
        this.$notify.error({
        title: "删除失败",
        customClass: "alter"
      });
      }else{
        clearTimeout(this.time)
        this.time = setTimeout(() => {
          this.load()
        }, 1000);
        
        this.$notify.success({
        title: "删除成功",
        customClass: "alter"
      });
      }
      
    },
    // 上传成功时
    upsucceed(res) {
      this.setLoadingFlage(false);
      // 判断是否上传成功
      if (res.ok == 0) {
        return this.$notify.error({
          title: "图片不能超过4M哟",
          customClass: "alter"
        });
      }
      this.imageUrl = res.src;
      // 成功提示
      this.$notify.success({
        title: "上传成功~",
        customClass: "alter"
      });
    },
    // 上传开始
    headupload() {
      // 开启加载
      this.setLoadingFlage(true);
    },
    uperror() {
      this.setLoadingFlage(false)
      this.$notify.error({
        title: "不明原因换个格式试一试",
        customClass: "alter"
      });
    },
    // 保存上传
    async addimg(){
      this.setLoadingFlage(true);
      // 判断
      if(this.imageUrl == '') {
        this.setLoadingFlage(false);
        return this.$notify.error({
          title: "请上传图片",
          customClass: "alter"
        });}
      let{data:res} = await this.$http.post('/imgs',{img:this.imageUrl,title:this.img_title})
      this.setLoadingFlage(false);
      if(res.ok == 0) return this.$notify.error({
        title: "保存失败~",
        customClass: "alter"
      });
      this.imageUrl = ''
      this.load()
      this.$notify.success({
        title: "保存成功~",
        customClass: "alter"
      });
    }
  },
  created() {
    if(this.USER_ID) {this.load()}else{
      this.$router.push('/')
    }
  },
  //   挂载完成
  mounted() {},
  // 监听
  watch: {
    allnavs: {
      handler(allnavs) {
        // 判断是否允许访问
        if (allnavs.navs && allnavs.navs.split(",")[2] != 1) {
          return this.$router.push("/");
        }
      },
      // 深度监听
      deep: true,
    }
  },
  USER_ID:{
     handler() {
       this.load()
      },
      immediate:true
  }
};
</script>

<style>
@keyframes iam {
  0% {
    font-size: 15px;
  }
  25% {
    font-size: 25px;
    color: red;
  }
  50% {
    font-size: 35px;
    color: red;
    font-weight: bold;
  }
  100% {
    font-size: 25px;
    color: red;
    font-weight: bold;
  }
}
/* 瀑布流测试区域 */
/* 媒体查询 */
@media (min-width: 800px) {
  div.pubuliu {
    -moz-column-count: 4; /* Firefox */
    -webkit-column-count: 4; /* Safari 和 Chrome */
    column-count: 4;
    width: 80% !important;
  }
}
@media (max-width: 800px) {
  div.pubuliu {
    -moz-column-count: 3; /* Firefox */
    -webkit-column-count: 3; /* Safari 和 Chrome */
    column-count: 3;
    column-gap: 10px;
  }
  div.pubuliu .item {
    margin-bottom: 10px;
    border-radius: 8px;
  }
}
@media (max-width: 420px) {
  div.pubuliu {
    -moz-column-count: 2; /* Firefox */
    -webkit-column-count: 2; /* Safari 和 Chrome */
    column-count: 2;
    column-gap: 10px;
  }
  div.pubuliu .item {
    margin-bottom: 10px;
    border-radius: 8px;
  }
}
.pubuliu {
  -moz-column-gap: 20px;
  -webkit-column-gap: 20px;
  column-gap: 20px;
  width: 96%;
  margin: 2em auto;
}
.pubuliu .item {
  position: relative;
  margin-bottom: 20px;
  -webkit-column-break-inside: avoid;
  break-inside: avoid;
  border-radius: 15px;
  overflow: hidden;
  transform: scale(1);
  transition: transform 0.8s;
}
.item.isdeletei {
  transform: scale(0);
}
.pubuliu .item_img {
  width: 100%;
}
/* 点赞区域 */
.pubuliu .img_bottom {
  height: 40px;
  line-height: 40px;
  background-color: #9fb2bf;
  position: relative;
  width: 100%;
  height: 60px;
  bottom: 0;
  left: 0;
  color: #f9cc9d;
  padding: 10px;
  padding-right: 50px;
  padding-bottom: 0px;
  white-space: normal;
}
.pubuliu .img_bottom i {
  position: absolute;
  right: 0;
  top: 0;
  width: 50px;
  height: 60px;
  text-align: center;
  line-height: 50px;
  font-size: 25px;
  cursor: pointer;
}
.pubuliu .img_bottom i .num {
  position: absolute;
  right: 15px;
  bottom: -13px;
  font-size: 15px;
}
.pubuliu .img_bottom i.active {
  animation: iam 0.5s ease;
  font-size: 25px;
  color: red;
  font-weight: bold;
}
.pubuliu .img_bottom i.active .num {
  color: crimson;
  font-weight: bold;
  font-size: 15px;
}
.pubuliu .addimg {
  padding: 15px;
  height: 340px;
  border: 2px solid #a5b4b4;
}
/* 删除按钮 */
.delete_btn {
  border: 2px solid #a5b4b4;
  height: 200px;
  text-align: center;
  line-height: 200px;
}
.delete_btn .delete {
  padding: 38px;
  font-size: 25px;
}
/* 遮罩 */
.pubuliu .zhezao {
  position: absolute;
  width: 0%;
  height: 100%;
  z-index: 1;
  background-color: rgba(0, 0, 0, 0.7);
  transition: width 0.3s;
}
.pubuliu .zhezao.show {
  width: 100%;
}
.pubuliu .zhezao .delete {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%); /*2d移动*/
}
/*  */
.Cucoloris .avatar-uploader .el-upload {
  width: 100%;
  height: 60%;
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}
.Cucoloris .avatar-uploader .el-upload:hover {
  border-color: #409eff;
}
.Cucoloris .avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 100%;
  height: 178px;
  line-height: 178px;
  text-align: center;
}
.Cucoloris .avatar {
  width: 100%;
  height: 178px;
  display: block;
}
</style>